	module.exports = {"data":{"post":{"html":"<p>This happened over 9 years ago, I think it's safe to tell the story now.</p>\n<p>When my wife bought our first restaurant, she set up a corporation (GmbH) for the new business. However, our lawyer was lazy, or slow, or something, and for our start of business the registration of the GmbH had not yet been approved by the local court. No registration, no firm, no business loan could be paid out. With a 7-figure purchase price due that day.</p>\n<p>No problem, says the bank, we'll book the purchase from your private account, even though our account balance was more like 3 figures and not 7. We'll increase your overdraft limit until the GmbH gets registered. No problem.</p>\n<p>So to this day I have a screenshot of electronic banking with an balance that day of minus one million Euros.</p>\n<p>We thought that was pretty funny until we tried to buy groceries and our bank card got denied. We had to call the bank again to increase our limit a bit more so we could eat.</p>","fields":{"title":"Million Euro Overdraft","date":"05 Aug 2016","url":"https://papascott.de/2016-08-05-million-euro-overdraft/"}}},"pathContext":{"slug":"/2016-08-05-million-euro-overdraft/","prev":{"fields":{"title":"1999.io is simple.  And simple.","date":"05 May 2016","slug":"/2016-05-05-1999io-is-simple-and-simple/"}},"next":{"fields":{"title":"Vinyl Anniversary","date":"06 Sep 2016","slug":"/2016-09-06-vinyl-anniversary/"}}}}

